	import Dialog from '../../wxcomponents/vant/dist/dialog/dialog';
	export default {
		data() {
			return {
				topShow:true,
				list: [],
                showSend: true,
				page:1,
				loadingThree:false,
				load:false,
				showTitle:false
			}
		},
		onPageScroll(e) {
			if(e.scrollTop>200){
				this.topShow = false
			}else{
				this.topShow = true
			}
		},
		onReachBottom(){
			if(this.load){
				this.load = false
				this.loadingThree = true
				this.page++
				this.getList()
			}
		},
		onLoad(e) {
			uni.showLoading({
			    title: '加载中'
			});
			
			 this.getList()
		},
		methods: {
			goSmallStretchPvw(id,item){
				console.log(item)
				if(item.is_sale_out == 1){
					uni.showToast({
					    title: '商品已下架，上架后可查看',
					    mask: "true",
						icon:'none'
					});
					
				}else{
					uni.navigateTo({
						url:'../../pagesMy/SmallStretchPvw/SmallStretchPvw?id='+id
					})
				}
				
			},
			// 重新编辑
			    edit (id) {
				  uni.navigateTo({
				  	url:'../../pagesMy/StretchIndexEdit/StretchIndexEdit?id='+id
				  })
			    },
			// 上架
			    putaway (item) {
			      item.is_sale_out = 0
			      this.$api.SmallStretchIsSaleOut({
			        token: uni.getStorageSync("token"),
			        user_id: uni.getStorageSync("userId"),
			        id: item.id,
			        type: 0
			      }).then(res => {
					  
					uni.showToast({
						title:'上架成功',
						icon:"none"
					})
			      })
			    },
			    // 下架
			    sold (item) {
			      item.is_sale_out = 1
			      this.$api.SmallStretchIsSaleOut({
			        token: uni.getStorageSync("token"),
			        user_id: uni.getStorageSync("userId"),
			        id: item.id,
			        type: 1
			      }).then(res => {
			        uni.showToast({
			        	title:'下架成功',
			        	icon:"none"
			        })
			      })
			    },
			getList () {
			      this.$api.SmallStretchMyCreate({
			        token: uni.getStorageSync("token"),
			        user_id: uni.getStorageSync("userId"),
			        page: this.page
			      }).then(res => {
			        if (res.data.code === '200') {
						uni.hideLoading();
						 if (this.page === 1) {
							  this.list = res.data.data
							  if(res.data.data.length == 0 || res.data.data.length < 20){
								  this.load = false
								  this.showTitle = true
							  }else{
								  this.load = true
							  }
						 } else {
								  this.list = this.list.concat(res.data.data)
								  this.loadingThree = false
							 if(res.data.data.length < 20){
								 this.load = false
								 this.loadingThree = false
								 this.showTitle = true
							 }else{
								 this.load = true
							 }
												
						 }
			        } else if (res.data.code === '1010') {
			          uni.clearStorageSync()
			          uni.showToast({
			              title: '登录过期，请重新登录',
			              duration: 2000,
			          	icon:'none'
			          });
			          
			          uni.reLaunch({
			          	url: '../../pages/login/login'
			          })
			        }
			      })
			    },
				// 发布
				Issue(){
					if (uni.getStorageSync('isVip') === 0) {
						Dialog.confirm({
						  title: '提示',
						  message: '您还不是VIP用户,没有发布权限',
						  confirmButtonText:'去支付'
						}).then(() => {
							uni.navigateTo({
								url:'../pay/pay'
							})
						}).catch(() => {
						  // on cancel
						});
					  } else if (uni.getStorageSync('isVip') === 1) {
						
						  uni.navigateTo({
						  	url:'../StretchIndexSend/StretchIndexSend'
						  })
						
					  } else {
						uni.clearStorageSync()
						uni.showToast({
						    title: '请登录',
						    duration: 2000,
							icon:'none'
						});
						
						uni.reLaunch({
							url: '../../pages/login/login'
						})
					  }
				},
				// 置顶
				fnscroll(){
					uni.pageScrollTo({
						scrollTop: 0,
						duration: 300
					})
				},
				 // 长按删除
				touch(item){
					Dialog.confirm({
					  title: '提示',
					  message: '确定要删除吗？',
					  confirmButtonText:'确定'
					}).then(() => {
						this.$api.SmallStretchVote({
						  token: uni.getStorageSync("token"),
						  user_id: uni.getStorageSync("userId"),
						  id: item.exhibition_id
						}).then(res => {
						  console.log(res)
						  this.myVote()
						  if (res.data.code === '1039') {
							uni.showToast({
								title:"删除成功",
								icon:'none'
							})
						    this.myVote()
						  } else {
						    uni.showToast({
						    	title:"删除失败",
						    	icon:'none'
						    })
						  }
						})	
					}).catch(() => {
					  // on cancel
					});
				},
		}
	}
